import uniPopup from '@/components/uni-popup/uni-popup.vue';
import uniPopupDialog from '@/components/uni-popup/uni-popup-dialog.vue';
import {
	getToken
} from '@/utils/auth';
import {
	mapState
} from 'vuex';
import {
	getUserInfo
} from '@/api/huizis.js';
import {
	getJoinStatus,
	getProjectDetail,
	joinProject
} from '@/api/share.js';

export default {
	data() {
		return {
			showStatus: 0,
			projectData: {},
			inviteName: "陈一发儿",
			projectId: 0,
			cid: 0,
			myName: "陈一发儿",
			mycid: 0,
		};
	},
	onLoad(options) {
		/* 初始化页面数据 */
		console.log(options)
		this.inviteName = options.inviteName;
		this.projectId = options.projectId;
		this.cid = options.cid;
		getProjectDetail({
			projectId: options.projectId
		}).then(response => {
			let resData = response.data.data;
			let initData = {
				currentNum: resData.currentNum,
				endTime: resData.endTime,
				monthCount: resData.monthCount,
				monthDays: resData.monthDays,
				name: resData.name,
				pid: resData.pid,
				returnValue: resData.returnValue,
				startTime: resData.startTime,
				totalNum: resData.totalNum
			};
			this.projectData = initData;
		});
		/* 判断用户是否登录 */
		if (getToken()) {
			/* 获取用户与分享项目之间的状态 */
			getJoinStatus({
				projectId: options.projectId
			}).then(response => {
				let statusCode = response.data.data;
				switch (statusCode) {
					case 0:
						this.showStatus = 0;
						break;
					case 1:
						this.showStatus = 1;
						break;
					case 2:
						this.showStatus = 2;
						break;
					case 3:
						this.showStatus = 3;
						break;
					default:
						break;
				}
			});
		} else {
			this.showStatus = 4;
			this.$refs.popup.open();
		}
	},
	onShareAppMessage: function(optiom) {
		/* 分享功能、未登录则引导登录*/
		if (getToken()) {
			if (optiom.from === 'button') {
				/* 获取用户本身的信息，作为分享者的数据，传递至被分享者页面 */
				getUserInfo().then(response => {
						let resData = response.data.data;
						this.myName = resData.nickName;
						this.mycid = resData.cid;
						return {
							title: '会子簿',
							desc: '专门记录会子的小程序',
							imageUrl: '../../static/images/cyf.jpg',
							path: '/pages/home/index?inviteName=' + this.myName + '&cid=' + this.mycid + '&projectId=' + this.projectId,
							success: function(res) {
								// 转发成功
								uni.showToast({
									title: '分享成功',
									icon: 'none'
								});
							},
							fail: function(res) {
								// 转发失败
								uni.showToast({
									title: '分享失败,请重试',
									icon: 'none'
								});
							}
						};
					})
					.catch(() => {
						uni.showToast({
							title: '分享失败,请重试',
							icon: 'none'
						});
					});
			}
		} else {
			this.$refs.popup.open();
		}
	},
	components: {
		uniPopup,
		uniPopupDialog
	},
	methods: {
		close(done) {
			done();
		},
		confirm(done) {
			uni.navigateTo({
				url: '/pages/login/index?from=share'
			});
			done();
		},
		toLogin() {
			this.$refs.popup.open();
		},
		/*  加入项目，传统分享者的数据 */
		join() {
			joinProject({
				projectId: this.projectId,
				inviter: this.cid
			}).then(response => {
				console.log(response)
			});
		}
	},
	filters: {
		formatDate: function(value) {
			var date = new Date(value); //时间戳为10位需*1000，时间戳为13位的话不需乘1000
			var Y = date.getFullYear() + '-';
			var M = (date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1) + '-';
			var D = date.getDate() + ' ';
			var h = date.getHours() + ':';
			var m = date.getMinutes() + ':';
			var s = date.getSeconds();
			return Y + M + D;
		}
	}
};
